<template>
  <div id="app">
    <section class="section">
      <div class="container">
        <div class="columns has-text-light" v-if="!accessToken">
          <div class="column is-8-desktop is-12-tablet has-text-centered has-background-dark">
            <h2 class="is-size-2">Dropbox Music Player</h2>
            <p id="welcome-p" class="is-size-5 has-text-weight-normal">
              Play audio from your Dropbox. DBXMP will find all your audio files and compile them in a single playlist for easy use and listening!
            </p>
            <a :href="authUrl" id="start-btn" class="button is-primary is-medium is-fullwidth">Start</a>
          </div>
        </div>

        <div class="columns has-text-light" v-if="accessToken">
          <div class="column is-8-desktop is-12-tablet has-text-centered has-background-dark">
              <h3 class="is-size-6">Now Playing</h3>
              <h3 id="current-song-audio__title" class="is-size-5">{{currentSong.title}}</h3>
              <div class="current-song__prev" @click="playSong(currentSong, 'prev')">
                <font-awesome-icon icon="step-backward" v-show="currentSong.title != 'No song.'" class="next-button__icon"/>
              </div>
              <div class="current-song__next" @click="playSong(currentSong, 'next')">
                <font-awesome-icon icon="step-forward" v-show="currentSong.title != 'No song.'" class="next-button__icon"/>
              </div>

              <div id="current-song-audio__container" v-show="currentSong.title != 'No song.'">
                <audio id="current-song-audio__audio" autoplay volume="0.2" ref="audio">
                    <source :src="currentSong.link">
                </audio>
                <controls :audio="$refs.audio"></controls>     
              </div>               
              <div class="playlist__container">
                <div class="box" :data-index="song.id" @click="playSong(song)" v-for="song in newPlaylist">                
                  <p>{{song.title}}</p>         
                </div>
                <div class="playlist-spinner" v-show="!Object.keys(this.newPlaylist).length">
                  <font-awesome-icon icon="spinner"/>
                </div>    
              </div>                  
          </div> 
        </div>
        
      </div>
    </section>
  </div>
</template>

<script>
  import 'buefy/dist/buefy.css';
 
  import 'isomorphic-fetch';
  var Dropbox = require('dropbox').Dropbox;

import Controls from './components/Controls';

  export default{
    components: {
      Controls
    },
    data() {
      return {
        accessToken: '',
        oldPlaylist: [],
        newPlaylist: [],
        queue: [],
        currentSong: {
          id: null,
          title: 'No song.',
          link: '',
        },
        playerOptions: {
          loop: false,
          shuffle: false,
          repeat: false,
        },
        audio: null,
        authUrl: '',
      }
    },
    watch: {
      'currentSong.id': (v, ov) => {
        const oldBox = document.querySelector('[data-index="'+ ov +'"]');
        const newBox = document.querySelector('[data-index="'+ v +'"]');
        const playlist = document.querySelector('.playlist__container');
        
        newBox.classList.add('box-now-playing');
        playlist.scrollTo({
          top: newBox.offsetTop - 100,
          behavior: 'instant'
        });

        if( oldBox ) {
          oldBox.classList.remove('box-now-playing');
        }        
      }
    },
    created() {
      document.title = 'Dropbox Music Player';

      const dbx = new Dropbox({ clientId: 'exzcryujcx4jgpu' })

      //check if auth'd
      if( this.getAccessTokenFromUrl() ) {
        this.accessToken = this.getAccessTokenFromUrl();
      
        //scan dropbox for all audio files
        this.filesListFolder();
      }else {        
        this.authUrl = dbx.getAuthenticationUrl('https://jonasbros.github.io/dbxmp/');
      }
      
     
    },
    mounted() {
      this.$refs.audio.onplay = () => {
        this.$root.$emit('playSong');
      }
      this.$refs.audio.onpause = () => {
        this.$root.$emit('pauseSong');
      }

      this.$root.$on('shufflePlayer', (btn) => {
        this.playerOptions.shuffle = !this.playerOptions.shuffle;

        if( this.playerOptions.shuffle ) {
          btn.closest('#shuffle-button').style.color = 'grey';    
        }else {
          btn.closest('#shuffle-button').style.color = '#FFF';    
        } 
      });

      this.$root.$on('repeatPlayer', (btn) => {
        this.playerOptions.repeat = !this.playerOptions.repeat;

        if( this.playerOptions.repeat ) {
          btn.closest('#repeat-button').style.color = 'grey';    
        }else {
          btn.closest('#repeat-button').style.color = '#FFF';    
        }       
      });
    },
    methods: {
      getAccessTokenFromUrl() {
        const start = 14;
        const end = window.location.hash.indexOf('&token_type') - 14;

        return window.location.hash.substr(start, end);
      },

      filesListFolder() {
        //init
        const dbx = new Dropbox({ accessToken: this.accessToken });
        //get all file list
        dbx.filesListFolder({ path: '', recursive: true })
        .then((response) => {
          this.saveAudioToPlayList(response.entries);
        }) 
        .catch((response) => {
          console.log(response);          
        });

      },

      saveAudioToPlayList(entries) {        
        //filter to remove folder types
        this.oldPlaylist = entries.filter( (el) => el['.tag'] == 'file' );
        //filter to remove none audio files
        this.oldPlaylist = this.oldPlaylist.filter( (el) => {
          //allowed file types
          const audioTypesList = [
            'aac', 'mp3', 'wav',
            'flac', 'alac', 'oog',
            'm4a', 'wma', 'm4r',
            'aiff', 'pcm'          
          ];

          const elFileExt = el.name.substring(el.name.lastIndexOf('.') + 1, el.name.length);

          if( audioTypesList.includes(elFileExt) ) {
            return true;
          }
        } );
        //find each file's temporary link
        this.oldPlaylist.forEach( (el) => {
          this.lookForFileLink(el.path_lower);
        } );
      },

      lookForFileLink(path) {
        //init
        const dbx = new Dropbox({ accessToken: this.accessToken });
        //get temporary link for file (we'll use the link as src on <audio>)
        return dbx.filesGetTemporaryLink({ path: path })
        .then((response) => {
          const song = {
            id: Object.keys(this.newPlaylist).length,
            title: response.metadata.name,
            link: response.link
          }
          this.newPlaylist.push(song);
        }) 
        .catch((response) => {
          console.log(response);          
        });
      },
      //play!
      playSong(song, pn = '') {      
        if( pn == 'prev' ) {
          song = this.newPlaylist[song.id - 1];
        }else if ( pn == 'next' ) {
          song = this.newPlaylist[song.id + 1];
        }

        if( pn ) {
          if( this.playerOptions.shuffle ) {
            let numberOfSongs = this.newPlaylist.length;
            let randomNum = Math.floor(Math.random() * numberOfSongs); 

            song = this.newPlaylist[randomNum];
          }

          if( this.playerOptions.repeat && pn != 'prev' ) {
            song = this.newPlaylist[this.currentSong.id];
          }
        }

        document.title = song.title;
        
        this.currentSong.link = null;

        this.currentSong.id = song.id;
        this.currentSong.title = song.title;
        this.$refs.audio.src = song.link; 
        
        console.log(song);

        //emit event for controls comp
        this.$root.$emit('playSong'); 
        //play next song after current song
        this.$refs.audio.onended = () => {
            this.$root.$emit('pauseSong');
            this.playSong(this.currentSong, 'next');                                   
        }

      },

    }

  }
</script>

