<template>
    <div id="controls__container" class="columns is-mobile">
        <div @click="togglePlay" id="play-button" class="column is-1">
            <font-awesome-icon icon="play" v-show="!isPlay" class="play-button__icon"/>
            <font-awesome-icon icon="pause" v-show="isPlay" class="play-button__icon"/>
        </div>

        <div @click="toggleShuffle($event)" id="shuffle-button" class="column is-1">
            <font-awesome-icon icon="random" class="play-button__icon"/>
        </div>  

        <div @click="toggleRepeat($event)" id="repeat-button" class="column is-1">
            <font-awesome-icon icon="retweet" class="play-button__icon"/>
        </div>        

        <div class="column is-2">
            <input 
                type="range"
                id="volume"                
                min="0"
                max="1"
                step="0.01"
                value="0.5"
                v-model="volume"
                @change="volumeChange"
            >        
        </div>
        
        <div class="column is-5">
            <input 
                type="range"
                id="seeker"           
                min="0"
                max="1"
                step="0.01"
                value="0.0"
                v-model="seeker"
                @change="seek"
            >
        </div>
        
        <div id="time" class="column is-2"><span>{{time.elapsed}}</span> / <span>{{time.duration}}</span></div>
        
    </div>
</template>

<script>

export default {
    name: 'Controls',
    props: ['audio'],
    data() {
        return {
            volume: 0.5,
            seeker: 0.0,
            time: {
                elapsed: '0:00',
                duration: '0:00',                
            },
            elapsedTimer: null,
            seekerTimer: null,
            isPlay: false,
        }
    },
    mounted() {
        this.$root.$on('playSong', () => {
            this.stopElapsedTimer();
            this.stopSeekTimer();

            this.audio.volume = this.volume;

            this.setDurationTime();  
            this.runElapsedTimer();
            this.runSeekTimer();

            this.isPlay = true;
        });

        this.$root.$on('pauseSong', () => {
            this.stopElapsedTimer();
            this.stopSeekTimer();

            this.isPlay = false;
        });                    
    }, 
    methods: {
        toggleRepeat(e) {
            this.$root.$emit('repeatPlayer', e.target);
        },
        toggleShuffle(e) {
            this.$root.$emit('shufflePlayer', e.target);
            
        },
        togglePlay() {
            if( this.audio.paused ) {
                this.audio.play();
                this.runElapsedTimer();
                this.runSeekTimer();
            }else {
                this.audio.pause();
                this.stopElapsedTimer();
                this.stopSeekTimer();
            }
        },
        volumeChange() {
            this.audio.volume = this.volume;
        },
        seek() {
            this.audio.currentTime = this.seeker * this.audio.duration;
        },
        setDurationTime() {
            let min = this.getMinute(this.audio.duration);       
            let sec = this.getSeconds(this.audio.duration);

            if( isNaN(min) || isNaN(sec) ) {
                min = '0';
                sec = '00';
            }

            this.time.duration = min + ':' + sec;
        },
        runElapsedTimer() {
            let min = 0;
            let sec = 0;

            this.elapsedTimer = setInterval( () => {                
                sec = this.getSeconds(this.audio.currentTime);
                min = this.getMinute(this.audio.currentTime);   
                this.time.elapsed = min + ':' + sec;

                if( this.audio.currentTime >=this.audio.duration ) {
                    this.time.elapsed = '0:00';
                }
            }, 1000 );            
        },
        stopElapsedTimer() {
            clearInterval(this.elapsedTimer);
        },
        runSeekTimer() {
            this.seekerTimer = setInterval( () => {
                this.seeker = this.audio.currentTime / this.audio.duration
            }, 1000 );
        },
        stopSeekTimer() {
              clearInterval(this.seekerTimer);
        },
        getMinute(time) {
            let min = time / 60;
            min = parseInt(min.toString().split('.')[0]);

            return min;
        },
        getSeconds(time) {
            let sec = Math.floor(time % 60);     

            if( sec < 10 ) {
                sec = '0' + sec.toString();
            }     

            return sec;
        }
    }
}

</script>

